
/************** Cards *************/
.card {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);

  .card-header {
    border-color: rgba(0, 0, 0, 0.15);

    font-weight: bold;
    font-size: 1.1rem;

    a:link,
    a:visited {
      background-color: transparent;
      color: var(--white);
      text-decoration: none;
    }

    // For sidebar menus
    @--sidebar {
      .card-body {
        @extend .card;
        padding: 0.5rem;
      }
    }
  }

  .card-text,
  .card-body {
    padding: 1rem;
  }

  // why the margin s
  @__bordered {

    border-color: rgba(0, 0, 0, 0.15);
    margin: 0 auto 1.2rem auto;
  }
}

/************** Carousel *************/

.carousel {
  min-height: 350px;
  margin: 0 auto;

  .carousel-control-prev,
  .carousel-control-next {
    width: 60%;
  }

  .carousel-item img {
    display: block;
    margin: 0 auto;
    height: 450px;
    width: 725px;
  }
}


/************** Icons *************/

.icon {
  // margin on right side only
  margin-right: 1.1rem;

  a:link,
  a:hover,
  a:visited {
    fill: white;
    color: white;
  }
  &__iconSM {
    @extend .icon;
    width: 32px;
    height: 32px;
  }

  &__iconLG {
    @extend .icon;
    width: 48px;
    height: 48px;
  }
}



/************** RSS Feed *************/

